'use strict';
import {COUPON_BG,CROSS,COMPANY_LOGO} from '@/config/image.js'
import NullData from '@/components/NullData.vue';
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue';
import {loadCoupon} from '@/api/decorateHome.js'


		
var _self
export default {
	components:{
		NullData,
		tkiQrcode
	},
	data() {
		return{
			img:[COUPON_BG,CROSS,COMPANY_LOGO],
			list:[],
			val: '',
			code:'',
			show:false,
			imglogo:''
		}
	},
	methods:{
		qaz(e){
			console.log(e)
		},
		saveToAlbum() {
			this.$refs.qrcode._makeCode();
			_self.show = !_self.show
		},
		qrR(path) {
			_self.code=path
			
			
		},
		useCoupon(e){
			console.log(e)
			let c=e.currentTarget.dataset.id
			console.log(2121)
			let count = 0
			count++
			if(count==1){
				_self.val='https://www.feiaizn.com/xj/index.html'+'?code='+c
				// console.log(_self.val)
				// uni.getImageInfo({
				//        src: COMPANY_LOGO,
				//        success: (res)=> {
				// 	console.log(res.path)
				// 	_self.imglogo=res.path
				// 	setTimeout(()=>{
				// 					this.saveToAlbum()
				// 				},100)
					 
				// }
				// })
				setTimeout(()=>{
									this.saveToAlbum()
								},100)
				// this.saveToAlbum()
			}
			
		},
		loadCouponNext(){
			loadCoupon().then(res=>{
				if(res.list){
					
					 this.getList(res.list)
				}
				
			})
		},
		handerCheckInChange: async function(e) {
		
			this.show = !this.show
			
			
			this.loadCouponNext()
			// this.getList(this.item)
		},
		arrayToObj(arr){
		 
		    var obj = {};
		    for(var i = 0; i < arr.length; i++){
		        obj[i] = arr[i];
		    }
		},


		getList(e){
			
			console.log(e)
			_self.list=e.map(res=>{
				let volume = res.volume.split(',')
				
				console.log(volume)
				let b=[]
				volume.map(reso=>{
					let qa=reso.split(';')
					let qaz=qa.map(re=>{
						return re.split(':')
					})
					console.log(qaz)
					let e=new Object()
					qaz.map(res=>{
						e[res[0]]=res[1]
					})
					console.log(e)
				
					let num= e.describe.replace(/[^0-9]/ig,"")
					e.num=num
					b.push(e)
				})
				
				return{
					...res,
					volume:b
				}
			})
			console.log(_self.list)
		}
		
	},
	 onLoad(options) {
		 _self=this
		this.getList(JSON.parse(options.data))
	}
};
